:root {
  --lightGreenFont: rgb(119, 233, 42);
  --yellowFont: yellow;
  --orangeFont: orange;
  --purpleFont: rgb(235, 80, 235);
  --silverFont: silver;
}

body,
.bgGradient {
  background: radial-gradient(rgb(173, 175, 120), rgb(31, 28, 28));
}

#aboutMe,
h2 {
  position: relative;
  z-index: 2;
}


