    rgba(61, 54, 54, 0.678)
  );
}
.lightGreenFont {
    color: rgb(119, 233, 42);
    color: var(--lightGreenFont);
}
.orangeFont {
    color: orange;
    color: var(--orangeFont);
}
.yellowFont {
    color: yellow;
    color: var(--yellowFont);
}
.purpleFont {
    color: rgb(235, 80, 235);
    color: var(--purpleFont);
}
.silverFont {
  color: silver;
  color: var(--silverFont);
}

.whiteFont {
  color: white;
}
body{
    margin: 0;
    padding: 0;
    overflow: hidden;
}
section{
    width: 100%;
    height: 100vh;
}
.leaves{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.leaves div{
    position: absolute;
    display: block;
}
.leaves div:nth-child(1){
    left: 20%;
    -webkit-animation: animate 15s linear infinite;
            animation: animate 15s linear infinite;
    -webkit-animation-delay: -7s;
            animation-delay: -7s;
}
.leaves div:nth-child(2){
    left: 50%;
    -webkit-animation: animate 17s linear infinite;
            animation: animate 17s linear infinite;
    -webkit-animation-delay: -5s;
            animation-delay: -5s;
}
.leaves div:nth-child(3){
    left: 70%;
    -webkit-animation: animate 19s linear infinite;
            animation: animate 19s linear infinite;
    -webkit-animation-delay: -3s;
            animation-delay: -3s;
}
.leaves div:nth-child(4){
    left: 0;
    -webkit-animation: animate 11s linear infinite;
            animation: animate 11s linear infinite;
    -webkit-animation-delay: -1s;
            animation-delay: -1s;
}
.leaves div:nth-child(5){
    left: 30%;
    -webkit-animation: animate 13s linear infinite;
            animation: animate 13s linear infinite;
}
.leaves div:nth-child(6){
    left: 60%;
    -webkit-animation: animate 20s linear infinite;
            animation: animate 20s linear infinite;
}
.leaves div:nth-child(7){
    left: 80%;
    -webkit-animation: animate 25s linear infinite;
            animation: animate 25s linear infinite;
}
.leaves div:nth-child(8){
    left: 10%;
    -webkit-animation: animate 18s linear infinite;
            animation: animate 18s linear infinite;
}

@-webkit-keyframes animate{
    0%{
        opacity: 0;
        top: -10%;
        /* transform: translateX(-20px) rotate(0deg); */
        transform: translateX(-5em) rotate(0deg);
    }
    10%{
        opacity: 1;
    }
    20%{
        /* transform: translateX(20px) rotate(60deg); */
        transform: translateX(5em) rotate(60deg);
    }
    100%{
        top: 110%;
    }
}

@keyframes animate{
    0%{
        opacity: 0;
        top: -10%;
        /* transform: translateX(-20px) rotate(0deg); */
        transform: translateX(-5em) rotate(0deg);
    }
    10%{
        opacity: 1;
    }
    20%{
        /* transform: translateX(20px) rotate(60deg); */
        transform: translateX(5em) rotate(60deg);
    }
    100%{
        top: 110%;
    }
}
.set2{
    transform: scale(1.5);
    /* transform: translateX(20px) rotateY(180deg); */
    transform: translateX(5em) rotateY(180deg);
    /* filter: blur(3px); */
    -webkit-filter: blur(0.3em);
            filter: blur(0.3em);
}
.set3{
    transform: scale(0.8);
    /* transform: translateX(20px) rotateX(180deg);  */
    transform: translateX(-5em) rotateX(180deg); 
    /* filter: blur(1px); */
    -webkit-filter: blur(0.05em);
            filter: blur(0.05em);
}


@media only screen and (max-width: 768px) {
    img {
        width: 50%;
    }
}
